    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    height: 100vh; /* Ensure the body takes the full height of the viewport */
}

ul {
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-inline-start: 0px;
}

li {
    list-style-type: none;
}

.mainContainer{
    display: flex;
    flex: 1; /* Allow the container to grow and fill the available space */
}

#headerContainer{
    display: flex;
    background-color: blue;
    flex: 0 0 auto; /* Ensure the header does not shrink or grow */
    width: 100%; /* Ensure the header takes full width */
    height: 70px;
}

.settingsBar{
    padding-left: 20px;
    padding-top: 10px;
}

.settingsBarItem{
    display: inline-block;
    margin-right: 10px;
}

.settingButton:hover{
    background-color: white;
}

.leftCol{
    flex-grow: 1; /* Allow leftCol to grow and fill the remaining space */
    background-color: black;
}

.toolBarList{
    width: 30px;
    background-color: grey;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.toolItem{
    padding-top: 1px;
    padding-bottom: 1px;
}

.toolButton:hover{
    background-color: white;
}

.centerCol {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Center the content vertically */
    width: 100vw;
    background-color: green;
}

.canvasWrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center; /* Center the canvases */
    overflow-y: scroll;
    overflow-x: scroll;
    width: 100%;
    height: 100%; /* Make sure it takes up all available space */
}

#backgroundCanvas, #pixelCanvas {
    position: absolute;
    display: block;
}

#backgroundCanvas {
    z-index: 1;
}

#pixelCanvas {
    z-index: 2;
}

.colorInfoContainer {
    display: flex;
}

.colorBox {
    width: 10px;
    height: 10px;
    border: 4px dotted black;
}

.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    padding-top: 60px;
}

.modal-content {
    background-color: #fefefe;
    margin: 5% auto; /* 15% from the top and centered */
    padding: 20px;
    border: 1px solid #888;
    width: 80%; /* Could be more or less, depending on screen size */
}

.close-button {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close-button:hover,
.close-button:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}
